html,body{margin:0;padding:0;overflow-x:hidden}.App-container{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;min-height:100vh;margin:3em 4.5em 3em;background:url(/static/media/gearbg.c1497ba9.png) no-repeat right 50% top 25%/70%}.footer-container{margin-top:4em;padding:2em}@media (max-width: 640px){.App-container{margin:0.5em;background:url(/static/media/gearbg.c1497ba9.png) no-repeat right 50% top 25%/100%}.footer-container{margin-top:2em;padding:0.5em}}

.home-container{min-height:100vh}.card-container{display:grid;grid-template-columns:1f 2fr 1fr;grid-gap:10px;grid-auto-rows:minmax(100px, auto);margin-top:1.5em}.first-item{grid-column:1 / 2;grid-row:1 / 4;padding:0.2em;width:9em}.first-item img{width:50%;height:4.5em}.second-item{grid-column:2 / 5;grid-row:1 / 4;text-align:center}.second-item :hover{cursor:pointer}.third-item{grid-column:5 / 6;grid-row:1 / 4;padding-top:2em;width:9em}.third-item img{width:100%;height:5.5em}.second-card-container{display:grid;grid-template-columns:1f 2fr 1fr;grid-gap:10px;grid-auto-rows:minmax(100px, auto)}.second-first-item{grid-column:1 / 2;grid-row:1 / 4;width:9em}.second-first-item img{width:100%;height:4.5em;margin-top:17em}.second-second-item{grid-column:2 / 5;grid-row:1 / 4;text-align:center}.second-second-item :hover{cursor:pointer}.second-second-item img{width:20em;height:6em;margin-bottom:2.5em}.second-third-item{grid-column:5 / 6;grid-row:1 / 4;padding-top:2em;width:9em}.second-third-item img{width:100%;height:5.5em;margin-top:15em}.third-card-container{display:grid;grid-template-columns:1f 2fr 1fr;grid-gap:10px;grid-auto-rows:minmax(100px, auto)}.third-first-item{grid-column:1 / 2;grid-row:1 / 4;width:9em}.third-first-item img{width:100%;height:4.5em;margin-top:17em}.third-second-item{grid-column:2 / 5;grid-row:1 / 4;text-align:center}.third-second-item :hover{cursor:pointer}.third-second-item img{width:15em;height:4em;margin-bottom:2em;margin:4em 0 4em}.third-third-item{grid-column:5 / 6;grid-row:1 / 4;padding-top:2em;width:9em}.third-third-item img{width:100%;height:5.5em;margin-top:15em}.home-container>h6{text-align:center;font-weight:bolder;color:#8a2be2}.covid19{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}.covid19 img{width:"100%";margin-right:1em}.covid19>div{display:-webkit-flex;display:flex;-webkit-justify-content:flex-start;justify-content:flex-start;margin-top:1em}.covid19 p{font-weight:bolder;margin-right:1.2em}.covid19CanadaDiv p{margin-right:2.1em;padding-top:0.7em}.covid19 p:nth-child(1){color:red}.covid19 p:nth-child(2){color:#000}.covid19 p:nth-child(3){color:blue}@media (max-width: 640px){.first-item{padding:2em 0 1em;grid-column:2 / 3;grid-row:1 / 2}.second-item{grid-column:1 / 4;grid-row:2 / 4}.third-item{grid-column:2 / 3;grid-row:4 / 5;padding-top:0}.second-first-item{grid-column:2 / 3;grid-row:1 / 2}.second-first-item img{margin-top:2em}.second-second-item{grid-column:1 / 4;grid-row:2 / 4}.second-third-item{grid-column:2 / 3;grid-row:4 / 5}.second-third-item img{margin-top:0}.third-card-container{display:none}.home-container h6{margin-top:2em}.covid19CanadaDiv p{margin-right:1.2em}}

.slide h1{transition:all 0.1s ease;-webkit-transform:translateY(-20px);transform:translateY(-20px);opacity:0}.slide button{transition:all 0.1s ease;-webkit-transform:translateY(20px);transform:translateY(20px);opacity:0}.slide p{transition:all 0.1s ease;-webkit-transform:translateY(20px);transform:translateY(20px);opacity:0}.slide section img{-webkit-transform:translateX(-10px);transform:translateX(-10px);opacity:0}.slide section span{-webkit-transform:translateY(-10px);transform:translateY(-10px);opacity:0}.slide section span strong{-webkit-transform:translateY(10px);transform:translateY(10px);opacity:0}.slide.animateIn.previous h1,.slide.current h1,.slide.animateIn.next h1,.slide.animateIn.previous button,.slide.current button,.slide.animateIn.next button,.slide.animateIn.previous p,.slide.current p,.slide.animateIn.next p,.slide.animateIn.previous section *{-webkit-transform:translateX(0);transform:translateX(0);transition-delay:.2s;opacity:1}

.inner button{-webkit-appearance:none;appearance:none;-webkit-filter:drop-shadow(0 5px 5px rgba(0,0,0,0.1));filter:drop-shadow(0 5px 5px rgba(0,0,0,0.1));-webkit-transition:all 0.5s ease;filter:drop-shadow(0 5px 5px rgba(0,0,0,0.1));transition:all 0.5s ease;border:none;background:#ffd800;border-radius:30px;text-transform:uppercase;box-sizing:border-box;padding:15px 40px;font-weight:400;font-size:13px;cursor:cursor}.inner button:hover{color:#ffffff;background:#222222;-webkit-filter:drop-shadow(0 0 5px rgba(0,0,0,0.2));filter:drop-shadow(0 0 5px rgba(0,0,0,0.2))}.wrapper{max-width:1335px;box-sizing:border-box;padding:0 20px;margin:auto}.wrapper a{display:inline-block;margin:5px}.wrapper a:first-child{margin-left:0}.slider-wrapper{font-family:"Lato", sans-serif;box-sizing:border-box;position:relative;height:60vh;overflow:hidden}.slide{height:60vh;background-size:cover !important}.slide::before{content:"";display:block;position:absolute;width:100%;height:100%;background:linear-gradient(transparent, rgba(0,0,0,0.9));bottom:0;left:0}.previousButton,.nextButton{position:absolute;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);z-index:10;background:url(/static/media/iconmonstr-arrow-24.804cbab1.svg) no-repeat center center/16px;width:50px;height:50px;text-indent:-9999px;cursor:pointer}.previousButton:hover,.nextButton:hover{background:url(/static/media/iconmonstr-arrow-24.804cbab1.svg) no-repeat center center/16px}.previousButton{left:0;-webkit-transform:rotate(180deg) translateY(calc(50% + 0px));transform:rotate(180deg) translateY(calc(50% + 0px))}.previousButton:hover{left:-10px}.nextButton{right:0}.nextButton:hover{right:-10px}.slider-content{text-align:center}.slider-content .inner{padding:0 70px;box-sizing:border-box;position:absolute;width:110%;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.slider-content .inner button{transition-delay:0s}.slider-content .inner h1{font-weight:900;margin:0 auto;max-width:840px;color:#ffffff;font-size:60px;line-height:1}.slider-content .inner p{color:#ffffff;font-size:20px;line-height:1.6;margin:20px auto 30px;max-width:640px}.slider-content section{position:absolute;bottom:20px;left:20px}.slider-content section span{color:#ffffff}.slider-content section span{color:rgba(255,255,255,0.5);font-size:12px;display:inline-block;text-align:left;line-height:1.4;vertical-align:middle;margin-left:10px}.slider-content section img{width:40px;height:40px;border:solid 2px rgba(255,255,255,0.5);border-radius:100%;vertical-align:middle}.slider-content section span strong{color:#ffffff;font-size:14px;display:block}@media (max-width: 740px){.slider-wrapper,.slide{height:27em}}@media (max-height: 600px){.inner{margin-top:20px}.slider-content .inner h1{font-size:30px}.slider-content .inner p{font-size:15px;line-height:1.5;margin:15px auto 15px}}@media (max-width: 640px){.inner{margin-top:20px}.slider-content .inner h1{font-size:30px}.slider-content .inner p{font-size:15px;line-height:1.5;margin:15px auto 15px}.inner{margin-top:20px}}

